import React, { useContext, useEffect, useState } from "react";
import { securedAxiosInstance } from "../../axiosConfig";
import Loader from "../../components/Loader";
import { AuthContext } from "../../context/AuthContext";
import styles from "../../css/CompanyNoticedetails.module.css";
import ReactQuill from "react-quill";
import "react-quill/dist/quill.snow.css";

const CompanyNoticeDetails = ({ match }) => {
  // const [activeNotice, setActiveNotice] = useState(0); // Set to 0 to open the first notice by default

  const [activeNotice, setActiveNotice] = useState(0);

  console.log(activeNotice);

  const toggleDropdown = (index) => {
    setActiveNotice((prevIndex) => (prevIndex === index ? prevIndex : index));
  };

  const { params } = match;
  const { state } = useContext(AuthContext);
  const [companyDetails, setCompanyDetails] = useState([]);

  const [submitStatus, setStatus] = useState({
    loading: false,
    success: false,
    error: false,
  });

  const fetchCompany = async () => {
    try {
      setStatus((current) => ({ ...current, loading: true }));
      const company = await securedAxiosInstance.get(
        `/announcements/${params.eventid}`
      );
      setCompanyDetails(company.data);
      setStatus((current) => ({ ...current, loading: false }));
    } catch (error) {
      console.log(error);
    }
  };

  useEffect(() => {
    fetchCompany();
  }, []);

  if (submitStatus.loading) return <Loader />;

  return (
    <>
      <div className={styles.outerdiv} style={{ padding: "20px" }}>
        <div className={styles.companybar} style={{ padding: "10px 20px" }}>
          <h6 style={{ margin: "0", fontSize: "1.2rem" }}>
            {companyDetails[0]?.for}
          </h6>
        </div>

        <div className={styles.wrapper} style={{ padding: "20px" }}>
          <div className={styles.contentdiv} style={{ marginBottom: "30px" }}>
            <div className={styles.hero}></div>
            <div
              className={`${styles.companylogo} d-flex justify-content-center align-items-center`}
              style={{ marginBottom: "20px" }}
            >
              <img
                src={`https://tnp-company-logos.s3.ap-south-1.amazonaws.com/${companyDetails.company?.Name}.png`}
                alt="Company Logo"
                style={{ maxWidth: "80%", borderRadius: "8px" }}
              />
            </div>
            <div className={styles.companycontentwrapper}>
              {companyDetails.map((notice, index) => (
                <div
                  key={index}
                  className={styles.noticeCard}
                  style={{
                    borderRadius: "8px",
                    backgroundColor: "#f9f9f9",
                    boxShadow: "0px 4px 8px rgba(0, 0, 0, 0.1)",
                  }}
                >
                  <div
                    className={styles.noticeHeader}
                    onClick={() => toggleDropdown(index)}
                    style={{
                      cursor: "pointer",
                      paddingBottom: "25px",
                      marginBottom: "10px",
                    }}
                  >
                    <div
                      style={{
                        display: "flex",
                        justifyContent: "space-between",
                      }}
                    >
                      <h5
                        className={styles.noticeTitle}
                        style={{ margin: "0", fontSize: "1.2rem" }}
                      >
                        {notice.title}
                      </h5>
                      <span
                        className={styles.noticeType}
                        style={{
                          backgroundColor: "#ffdf7e",
                          color: "#333",
                          padding: "5px 15px",
                          borderRadius: "12px",
                          fontWeight: "bold",
                          marginLeft: "10px",
                          fontSize: "0.9rem",
                        }}
                      >
                        {notice.event_type}
                      </span>
                    </div>

                    {activeNotice === index && (
                      <div
                        className={styles.noticeContentWrapper}
                        style={{
                          padding: "20px",
                          borderRadius: "8px",
                          boxShadow: "0px 4px 10px rgba(0, 0, 0, 0.1)",
                          marginTop: "10px",
                          border: "1px solid #ddd",
                          backgroundColor: "#fff",
                        }}
                      >
                        <p
                          className={styles.noticeContent}
                          style={{ marginBottom: "15px", lineHeight: "1.6" }}
                          id="editor"
                        >
                          {
                            <ReactQuill
                              style={{ border: "none", outline: "none" }}
                              theme="bubble"
                              modules={{ toolbar: false }}
                              readOnly={true}
                              value={JSON.parse(notice.content)}
                            />
                          }
                        </p>
                        <div
                          className={styles.noticeMeta}
                          style={{ marginBottom: "10px" }}
                        >
                          <span
                            style={{
                              display: "block",
                              marginBottom: "5px",
                              fontSize: "0.9rem",
                            }}
                          >
                            <strong>Published:</strong>{" "}
                            {new Date(notice.published_at).toLocaleDateString()}
                          </span>
                        </div>
                        {notice.attachment.length > 0 && (
                          <div className={styles.noticeAttachments}>
                            <h5
                              style={{
                                marginTop: "15px",
                                marginBottom: "10px",
                                fontSize: "1rem",
                              }}
                            >
                              Attachments:
                            </h5>
                            {notice.attachment.map((sn, index) => (
                              <div
                                key={index}
                                className={`${styles.file} mb-2`}
                                style={{
                                  cursor: "pointer",
                                  display: "flex",
                                  alignItems: "center",
                                  padding: "10px",
                                  borderRadius: "6px",
                                  backgroundColor: "#f5f5f5",
                                  marginBottom: "10px",
                                }}
                                onClick={() => window.open(sn.url)}
                              >
                                <div
                                  className={styles.fileicon}
                                  style={{ marginRight: "10px" }}
                                >
                                  <img src="/pdf.png" width="30px" alt="pdf" />
                                </div>
                                <div
                                  className={styles.filetitle}
                                  style={{ flex: "1", fontSize: "0.9rem" }}
                                >
                                  {sn.name}
                                </div>
                                <div className={styles.downloadicon}>
                                  <i
                                    style={{
                                      color: "#1456cf",
                                      fontSize: "20px",
                                    }}
                                    className="bi bi-download"
                                  ></i>
                                </div>
                              </div>
                            ))}
                          </div>
                        )}
                      </div>
                    )}
                  </div>
                </div>
              ))}
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default CompanyNoticeDetails;
